import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {

    getConvocatoria(ctx, idConvocatoria) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-social/beca/convocatoria/${idConvocatoria}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

  },
}
